@import "https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap";
body {
  color: #fff;
  pointer-events: none;
  background-color: #000;
  margin: 0;
  font-family: "Press Start 2P", cursive;
}

#loadingScreen {
  text-align: center;
  z-index: 1;
  background-color: #000;
  width: 100%;
  height: 100vh;
  transition: opacity 1s;
  display: block;
  position: absolute;
}

.loadingImage {
  background-image: url("loading.ce873fe5.gif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 256px;
  height: 256px;
}

#titleScreen {
  z-index: 0;
  visibility: visible;
  text-align: center;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding-top: 30px;
  font-size: 2em;
  font-weight: 400;
  transition: opacity .5s;
  display: flex;
  position: absolute;
  top: 0;
}

#titleScreen .highscore {
  text-align: left;
  font-size: 20px;
  line-height: 10px;
}

#titleScreen .title {
  font-size: 28px;
  line-height: 28px;
}

#titleScreen b, #highscoreValue {
  color: #e23a4d;
}

#titleScreen .title b {
  animation: 1s infinite forwards pulse;
}

#leaderboard p {
  margin-top: ;
  animation: 1s infinite forwards pulse;
}

#titleScreen .leaderboard {
  pointer-events: auto;
  background-image: url("leaderboardIcon.cb01e36f.png");
  width: 48px;
  height: 48px;
  transition: transform .1s;
  transform: scale(1);
}

#titleScreen .leaderboard:hover {
  transform: scale(1.1);
}

#gameScreen {
  visibility: visible;
  text-align: center;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  min-height: 140px;
  padding-top: 20px;
  transition: opacity .5s;
  display: flex;
  position: absolute;
  top: 0;
}

.screen {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: absolute;
}

#nameScreen h1 {
  text-align: center;
  margin-bottom: 20px;
}

#playerNameInput {
  color: #fff;
  background-color: #000;
  border: 3px double;
  margin-bottom: 10px;
  padding: 10px;
  font-family: inherit;
  font-size: 16px;
}

#startButton {
  color: #fff;
  background-color: #000;
  border: 3px double;
  padding: 10px 20px;
  font-family: inherit;
  font-size: 16px;
}

#startButton:hover {
  color: #000;
  cursor: pointer;
  background-color: #fff;
}

#errorMessage {
  color: red;
  margin-top: 10px;
}

#leaderboard {
  visibility: visible;
  text-align: center;
  z-index: 1;
  background-color: #141414bf;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: calc(100vh - 30px);
  padding-top: 30px;
  font-weight: 400;
  transition: opacity .5s;
  display: flex;
  position: absolute;
}

#leaderboard h1 {
  margin-bottom: 60px;
}

#leaderboard-list {
  text-transform: uppercase;
  font-size: 30px;
  list-style-type: none;
}

#leaderboard-list li {
  margin-bottom: 15px;
}

#score {
  font-size: 35px;
}

#nameScreen {
  visibility: collapse;
  z-index: 1;
  background-color: #141414bf;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: absolute;
}

#gameOver {
  color: #e23a4d;
  text-align: center;
  background-color: #141414bf;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 70px;
  font-weight: 400;
  transition: opacity .5s;
  display: flex;
  position: absolute;
}

@keyframes pulse {
  0% {
    opacity: 1;
  }

  50% {
    opacity: .2;
  }

  100% {
    opacity: 1;
  }
}

@keyframes sparkle {
  0%, 100% {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #f0f, 0 0 30px #f0f, 0 0 40px #f0f, 0 0 50px #f0f, 0 0 60px #f0f, 0 0 70px #f0f;
  }

  50% {
    text-shadow: 0 0 2px #fff, 0 0 4px #fff, 0 0 6px #f0f, 0 0 8px #f0f, 0 0 10px #f0f, 0 0 12px #f0f, 0 0 14px #f0f, 0 0 16px #f0f;
  }
}

.sparkle {
  animation: 1.5s infinite alternate sparkle;
}

#copyright {
  text-align: center;
  width: 100%;
  font-size: .8em;
  position: absolute;
  bottom: 10px;
}

@media (width <= 900px) {
  #titleScreen {
    flex-direction: column;
    font-size: 1.5em;
  }

  #titleScreen .highscore {
    font-size: 15px;
    line-height: 8px;
  }

  #titleScreen .title {
    font-size: 22px;
    line-height: 22px;
  }

  #gameScreen {
    flex-direction: column;
  }

  #leaderboard-list {
    font-size: 20px;
  }
}
